___CSS_LOADER_EXPORT___.push([module.id, `.Home-hero {
    position: relative;
  }
  
  .HomeImg-hero {
    object-fit: cover;
  }
  
  .articleTagLine-hero {
    position: absolute;
    top: 55px;
    left: 100px;
    color: #f5f5f5;
    font-size: 64px;
    text-shadow: 0px 0px 3px #070707;
  }
  
  #ArticleList {
    width: 100%;
    padding: 3rem 5rem 3rem 5rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
  }
  
  .article-card {
    background-color: #f5f5f5;
    border: 1px solid #4fc0d0;
    border-radius: 10px;
    padding: 1.5rem;
  }
  
  .tips-card img {
    border-radius: 5px;
  }
  
  .tips-info {
    padding: 1rem 1rem 0 1rem;
  }
  
  .tips-info p {
    height: 100px;
    overflow: hidden;
    text-overflow: ellipsis;
  }
