___CSS_LOADER_EXPORT___.push([module.id, `.forum-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.forum-container h2 {
  width: 80%;
  background-color: #4299a5;
}

.forum-container p {
  color: #4299a5;
}

.forumChat-container {
  width: 90%;
}

.chatbox {
  height: 550px;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  object-fit: cover;
  padding: 1rem;
  overflow-y: auto;
}

.chatbox::-webkit-scrollbar {
  width: 0px;
}

/* chat masuk */
.chat-incoming {
  display: flex;
  flex-direction: column;
  align-items: start;
}

.chat-incoming .chatProfile {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: start;
}

.chat-incoming .chatProfile img {
  padding-right: 10px;
}

.chat-incoming .chatProfile p {
  margin: 0;
  color: #2b646b;
}

/* chat keluar */
.chat-outcoming {
  display: flex;
  flex-direction: column;
  align-items: end;
}

.chat-outcoming .chatProfile {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: end;
}

.chat-outcoming .chatProfile img {
  padding-left: 10px;
}

.chat-outcoming .chatProfile p {
  margin: 0;
  color: #2b646b;
}

/* memasukkan pesan */
.input-chat {
  padding: 1rem 2rem 1rem 2rem;
}

form {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
}

.input-chat form .message {
  width: 90%;
  padding: 7px 15px 7px 15px;
  font-size: 20px;
  border: 1px solid #4299a5;
  border-radius: 10px;
}

.input-chat form .message:focus {
  outline: 1px solid #2a6269;
}

.input-chat form .image {
  width: 44px;
  height: 44px;
}
