___CSS_LOADER_EXPORT___.push([module.id, `.contact-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

.contact-hero-img{
  object-fit: cover;
}

.contact-hero {
    width: 100%;
    height: 350px;
    background-size: cover;
    background-position: 0rem;
    border-radius: 10px;
    font-size: 64px;
    padding: 5rem 0 0 5rem;
    text-shadow: 0px 0px 2px #ffffff;
  }
  .contact-container h2 {
    margin-top: 10vh;
    width: 20%;
    background-color: #4299a5;
  }
  
  .content{
    display: flex;
    width: 100%;
    max-width: 1000px;
    justify-content: center;
    align-items: center;
  }

  .team{
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 16px;
      margin: 32px 0;
  }

  .team-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    border-radius: 8px;
    overflow: hidden;
    margin: 1vh;
    text-align: center;
}

.team-item__header {
    position: relative;
}

.team-item .team-item__header .team-item__header__poster {
    width: 100%;
}

.team-item .team-item__header .team-item__header__rating {
    position: absolute;
    padding: 8px;
    bottom: 20px;
    left: 0;
    display: inline-block;
    background-color: #2D2424;
    color: whitesmoke;
}

.team-item .team-item__content {
    padding: 16px;
}

.team-item .team-item__content h3 {
    margin: 0 0 10px 0;
    font-size: 1.5em;
}

.team-item .team-item__content p {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
}

.username{
  color: #4299a5;
}

@media screen and (max-width: 452px) {
  .team {
      grid-template-columns: 1fr;
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/contact-us.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,SAAS;AACb;;AAEA;EACE,iBAAiB;AACnB;;AAEA;IACI,WAAW;IACX,aAAa;IACb,sBAAsB;IACtB,yBAAyB;IACzB,mBAAmB;IACnB,eAAe;IACf,sBAAsB;IACtB,gCAAgC;EAClC;EACA;IACE,gBAAgB;IAChB,UAAU;IACV,yBAAyB;EAC3B;;EAEA;IACE,aAAa;IACb,WAAW;IACX,iBAAiB;IACjB,uBAAuB;IACvB,mBAAmB;EACrB;;EAEA;MACI,aAAa;MACb,qCAAqC;MACrC,SAAS;MACT,cAAc;EAClB;;EAEA;IACE,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,WAAW;IACX,0CAA0C;IAC1C,kBAAkB;IAClB,gBAAgB;IAChB,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,kBAAkB;IAClB,YAAY;IACZ,YAAY;IACZ,OAAO;IACP,qBAAqB;IACrB,yBAAyB;IACzB,iBAAiB;AACrB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA;IACI,gBAAgB;IAChB,uBAAuB;IACvB,oBAAoB;IACpB,qBAAqB;IACrB,4BAA4B;AAChC;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE;MACI,0BAA0B;EAC9B;AACF","sourcesContent":[".contact-container{\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n    gap: 2rem;\r\n}\r\n\r\n.contact-hero-img{\r\n  object-fit: cover;\r\n}\r\n\r\n.contact-hero {\r\n    width: 100%;\r\n    height: 350px;\r\n    background-size: cover;\r\n    background-position: 0rem;\r\n    border-radius: 10px;\r\n    font-size: 64px;\r\n    padding: 5rem 0 0 5rem;\r\n    text-shadow: 0px 0px 2px #ffffff;\r\n  }\r\n  .contact-container h2 {\r\n    margin-top: 10vh;\r\n    width: 20%;\r\n    background-color: #4299a5;\r\n  }\r\n  \r\n  .content{\r\n    display: flex;\r\n    width: 100%;\r\n    max-width: 1000px;\r\n    justify-content: center;\r\n    align-items: center;\r\n  }\r\n\r\n  .team{\r\n      display: grid;\r\n      grid-template-columns: repeat(3, 1fr);\r\n      gap: 16px;\r\n      margin: 32px 0;\r\n  }\r\n\r\n  .team-item {\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    width: 100%;\r\n    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n    